import Emitter from 'licia/Emitter';
import uniqId from 'licia/uniqId';
import { IMessenger, isSyncApi, log } from '@tencent/wxa-runtime-base';

const logger = log('asBridge');

export default function (messenger: IMessenger) {
  const onEmitter = new Emitter();
  const subscribeEmitter = new Emitter();
  const callbacks: any = {};

  messenger.on('message', async (msg: any) => {
    const { command, data, webviewId } = msg;

    switch (command) {
      case 'APPSERVICE_ON_EVENT': {
        onEmitter.emit(data.eventName, data.data, webviewId);
        break;
      }
      case 'APPSERVICE_INVOKE_CALLBACK': {
        const { callbackId, res } = data;

        if (callbacks[callbackId]) {
          callbacks[callbackId](res);
          delete callbacks[callbackId];
        }
        break;
      }
      case 'WEBVIEW_PUBLISH': {
        subscribeEmitter.emit(data.eventName, data.data, webviewId);
        break;
      }
    }
  });

  return {
    invoke(api: string, args: any, callback: any) {
      const msg: any = {
        command: 'APPSERVICE_INVOKE',
        data: {
          api,
          args,
        },
      };

      if (isSyncApi(api)) {
        const result = messenger.sendSync(msg);
        logger.debug('invoke sync', api, args, result.errMsg, result);
        callback(result);
      } else {
        const callbackId = uniqId();
        logger.debug('invoke', api, callbackId, args);
        callbacks[callbackId] = callback;
        msg.data.callbackId = callbackId;

        messenger.send(msg);
      }
    },
    publish(eventName: string, data: any, webviewIds: string[]) {
      logger.debug('publish', eventName, data, webviewIds);
      data = JSON.parse(JSON.stringify(data));

      messenger.send({
        command: 'APPSERVICE_PUBLISH',
        data: {
          eventName,
          data,
          webviewIds,
        },
      });
    },
    subscribe(eventName: string, callback: any) {
      logger.debug('subscribe', eventName);
      subscribeEmitter.on(eventName, callback);
    },
    on(api: string, callback: any) {
      logger.debug('on', api);
      onEmitter.on(api, callback);
    },
  };
}
