import { IWxConfig } from '../common/config';
import { IframeMessenger } from '@tencent/wxa-runtime-base';
import loadJs from 'licia/loadJs';
import createBridge from './lib/createBridge';

export class Appservice {
  private messenger: IframeMessenger;
  constructor() {
    this.rewriteUa();
    this.initMessenger();
    this.initBridge();

    this.messenger.send({
      command: 'APPSERVICE_CREATED',
    });
  }
  injectScript(src: string) {
    return new Promise((resolve) => {
      loadJs(src, () => {
        resolve(null);
      });
    });
  }
  injectWxConfig(wxConfig: IWxConfig) {
    window.__wxConfig = wxConfig;
  }
  private initBridge() {
    window.WeixinJSBridge = createBridge(this.messenger);
  }
  private initMessenger() {
    this.messenger = new IframeMessenger(window.parent, this.handleMsgSync);
  }
  private rewriteUa() {
    Object.defineProperty(window.navigator, 'userAgent', {
      get() {
        return 'wechatdevtools appservice';
      },
    });
  }
  private handleMsgSync = (msg: any) => {
    switch (msg.command) {
      case 'CALL_METHOD':
        return this.handleCallMethodSync(msg.data);
    }
  };
  private handleCallMethodSync(data: any) {
    const { name, args } = data;
    const method = (this as any)[name];
    return method.call(this, ...args);
  }
}

declare global {
  interface Window {
    appservice: Appservice;
    WeixinJSBridge: any;
  }
}

window.appservice = new Appservice();
